html {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

body {
  font-family: "sf", "Arial", sans-serif;
  cursor: default;
  height: 100%;
  width: 900px;
}

h1 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 30px;
  border-bottom: 1px solid grey;
}

@font-face {
  font-family: "sf";
  src: url("../fonts/sf-pro-display-medium.woff2") format("woff2"),
  url("../fonts/sf-pro-display-medium.woff") format("woff");
  font-weight: 450;
  font-style: normal;
  font-display: swap;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/*Ночная тема*/

.page__body--night {
  color: var(--grey__text);
  background-color: var(--darken__night-theme);
}
